<template>
  <div class="vuecorpper">
    <div class="cropper-content">
      <div class="cropper">
        <vueCropper
          ref="cropper"
          :img="chooseimg"
          :outputSize="option.size"
          :outputType="option.outputType"
          :info="true"
          :full="option.full"
          :canMove="option.canMove"
          :canMoveBox="option.canMoveBox"
          :original="option.original"
          :autoCrop="option.autoCrop"
          :autoCropWidth="option.autoCropWidth"
          :autoCropHeight="option.autoCropHeight"
          :fixedBox="option.fixedBox"
          @realTime="realTime"
        ></vueCropper>
      </div>
      <!--  {'width':previews.w+'px','height':previews.h+'px','overflow':'hidden','margin':'5px'}-->
      <!-- previews.img = data.img   -->
      <!-- previews.url blob img -->
      <div class="show-preview">
        <div :style="previews.div" class="preview">
          <img :src="previews.url" :style="previews.img" />
        </div>
      </div>
    </div>
    <el-row style="margin-top:20px" type="flex" justify="center">
      <!--         <el-button style="margin-right:20px">
            <label class="btn" for="uploads"></label>
      </el-button>-->
      <input
        type="file"
        id="uploads"
        style="position:absolute; clip:rect(0 0 0 0);"
        accept="image/png, image/jpeg, image/gif, image/jpg"
        @change="uploadImg($event, 1)"
      />
      <el-button type="primary" icon="el-icon-zoom-in" circle @click="changeScale(1)"></el-button>
      <el-button type="primary" icon="el-icon-zoom-out" circle @click="changeScale(-1)"></el-button>
      <el-button type="primary" icon="el-icon-refresh-left" circle @click="rotateLeft"></el-button>
      <el-button type="primary" icon="el-icon-refresh-right" circle @click="rotateRight"></el-button>
      <el-button @click="down('blob')"></el-button>
    </el-row>
  </div>
</template>

<script>
import { VueCropper } from "vue-cropper";
export default {
  props: {
    chooseimg: String,
    imgname:String
  },
  components: {
    VueCropper
  },
  data() {
    return {
      previews: {},
      previewStyle1: {},
      option: {
        full: false, // propsfull
        canMove: true,
        canMoveBox: true,
        autoCropWidth: 150,
        autoCropHeight: 150,
        img:
          "http://img1.vued.vanthink.cn/vued751d13a9cb5376b89cb6719e86f591f3.png", // 
        info: true, // 
        outputSize: 0.8, // 
        outputType: "png", // 
        canScale: false, // 
        autoCrop: true, // 
        // autoCropWidth: 300, // 
        // autoCropHeight: 200, // 
        fixedBox: true, //  
        fixed: true, // 
        fixedNumber: [7, 5], // 
        original: false, // 
        centerBox: false, // 
        infoTrue: true // true  false 
      }
    };
  },
  methods: {
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    changeScale(num) {
      this.$refs.cropper.changeScale(num);
    },
    uploadImg() {},
    realTime(data) {
      console.log(data);
      /**
       * data ={
          div, 
          h,
          html,  html
          img, ,
          url, blob img
          w,
        }
       */
      this.previewStyle1 = {
        width: data.w + "px",
        height: data.h + "px",
        overflow: "hidden",
        margin: "0",
        zoom: 1
      };
      this.previews = data;
    },
    down(type) {
        this.$refs.cropper.getCropBlob(data => {
        let formData = new FormData()
        formData.append("file", data, this.imgname);
        this.axios
          .post("http://www.rorscloud.top:4000/admin/upload", formData).then(res=>{
              let { rel_img,abs_img } =res.data
              this.$emit('after-upload',{rel_img,abs_img})
          })

      });
    }
  }
};
</script>
<style lang="scss" scoped>
.cropper-content {
  display: flex;
  justify-content: flex-end;
  .cropper {
    width: 400px;
    height: 300px;
  }
  .show-preview {
    flex: 1;
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    .preview {
      overflow: hidden;
      border: 2px solid #ff4406;
      background: #cccccc;
      margin-left: 40px;
    }
  }
}
.footer-btn {
  margin-top: 30px;
  display: flex;
  justify-content: flex-start;
  .scope-btn {
    width: 350px;
    display: flex;
    justify-content: space-between;
    .upload-btn {
      flex: 1;
      display: flex;
      justify-content: center;
    }
    .btn {
      outline: none;
      display: inline-block;
      line-height: 1;
      white-space: nowrap;
      cursor: pointer;
      text-align: center;
      box-sizing: border-box;
      outline: 0;
      margin: 0;
      -webkit-transition: 0.1s;
      transition: 0.1s;
      font-weight: 500;
      padding: 8px 15px;
      font-size: 12px;
      border-radius: 3px;
      color: #fff;
      background-color: #67c23a;
      border-color: #67c23a;
    }
  }
}
</style>